<template>
  <div style="padding-top: 51px;">
    <pr-row>
      <pr-col span="12">
        <section>
          <h1></h1>
          <hr/>
          <h2>have a try!</h2>
          <div class="pr-form-group">
            <div class="pr-form-item">
              <span class="pr-form-item-label">
                <i class="pr-required"></i>
                
              </span>
              <span class="pr-form-item-input">
                <pr-input
                  v-model="formData.username"
                  type="text"
                  placeholder="2-10"
                  width="300"
                />
              </span>
            </div>

            <div class="pr-form-item">
              <span class="pr-form-item-label">
                <i class="pr-required"></i>
                
              </span>
              <span class="pr-form-item-input">
                <pr-input
                  v-model="formData.email"
                  type="text"
                  placeholder=""
                  width="300"
                />
              </span>
            </div>

            <div class="pr-form-item">
              <span class="pr-form-item-label">
                <i class="pr-required"></i>
                
              </span>
              <span class="pr-form-item-input">
                <pr-input
                  v-model="formData.mobile"
                  type="text"
                  placeholder=""
                  width="300"
                />
              </span>
            </div>

            <div class="pr-form-item">
              <span class="pr-form-item-label"> </span>
              <span class="pr-form-item-input">
                <pr-button
                  type="primary"
                  @click="handleSave"
                >
                  
                </pr-button>
              </span>
            </div>
          </div>
          <div style="padding-left: 80px;">
            <p>{{msg}}</p>
            <p>{{validator.validateKey}}</p>
            <p>{{validator.validateResult}}</p>
          </div>
        </section>
      </pr-col>
      <pr-col span="12">
        <Example />
      </pr-col>
    </pr-row>
  </div>
</template>

<script>
import Example from "../docs/validator/index/example.md";
export default {
  components: {
    Example
  },
  data() {
    return {
      formData: {
        username: "",
        email: "",
        mobile: ""
      },
      validator: new this.$Validator({
        username: [
          {
            required: true,
            message: "username"
          }
        ],
        email: [
          {
            required: true,
            message: ""
          },
          {
            type: "email",
            message: ""
          }
        ],
        mobile: [
          {
            required: true,
            message: ""
          },
          {
            type: "chinaMobile",
            message: ""
          }
        ]
      }),
      msg: ""
    };
  },
  methods: {
    handleSave() {
      this.validator
        .validate(this.formData)
        .then(() => {
          this.msg = "";
        })
        .catch(msg => {
          this.msg = msg;
        });
    }
  }
};
</script>

<style lang="less">
.pr-col-12 {
  max-height: calc(100vh - 91px);
  overflow: auto;
}
.pr-form-group {
  margin: 20px auto;
}

.pr-form-item-label {
  width: 120px;
}

.demo-container {
  height: auto;
}
.docs-trans {
  display: none;
}
</style>



